.container {
  width: 100%;
  height: 100vh;
  font-family: sans-serif;
  background-color: rgb(187, 187, 245);
  color: #fff;
  align-items: center;
  /* display: flex; */
  justify-content: center;
}
.left,
.right {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card {
  width: 350px;
  height: 460px;
  border-radius: 20px;
  box-shadow: 0 0 40px 20px rgba(0, 0, 0, 0.26);
  perspective: 1000px;
}

.card-front {
  position: absolute;
  width: 100%;
  height: 460px;
  background-position: center;
  background-size: cover;
  border-radius: 20px;
  background-image: linear-gradient(
    rgba(54, 54, 148, 0.8),
    rgba(54, 54, 148, 0.8)
  );
  padding: 55px;
  box-sizing: border-box;
  backface-visibility: hidden;
}
.card h2 {
  font-weight: normal;
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}
.input-box {
  width: 100%;
  background: transparent;
  border: 2px solid #fff;
  margin: 6px 0;
  height: 32px;
  border-radius: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  outline: none;
  text-align: center;
  color: #fff;
}
.select-box {
  width: 100%;
  background: transparent;
  border: 2px solid #fff;
  margin: 6px 0;
  height: 32px;
  border-radius: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  outline: none;
  text-align: center;
  color: #fff;
}

::placeholder {
  color: #fff;
  font-size: 12px;
}
.submit-btn {
  position: relative;
  width: 100%;
  background: transparent;
  border: 2px solid #fff;
  margin: 35px 0 10px;
  height: 30px;
  font-size: 12px;
  border-radius: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  outline: none;
  color: rgba(49, 49, 125, 0.8);
  cursor: pointer;
}
.signin {
  position: relative;
  width: 100%;
  /* background: transparent; */
  background-color: rgba(49, 49, 125, 0.8);
  border: 2px solid #fff;
  margin: 35px 0 10px;
  height: 34px;
  font-size: 12px;
  border-radius: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  outline: none;
  color: #fff;
  cursor: pointer;
}
span {
  font-size: 12px;
}

.card .btn {
  margin-top: 70px;
}

.card a {
  color: #fff;
  text-decoration: none;
  display: block;
  text-align: center;
  font-size: 13px;
  margin-top: 8px;
}
