.container {
  width: 100%;
  height: 100vh;
  font-family: sans-serif;
  background-color: rgb(187, 187, 245);
  color: #fff;
}
nav {
  background-color: rgba(54, 54, 148, 0.8);
  height: 80px;
  width: 100%;
}
label.logo {
  color: white;
  font-size: 35px;
  line-height: 80px;
  padding: 0 100px;
  font-weight: bold;
}

nav ul {
  float: right;
  margin-right: 20px;
}
nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
}
nav ul li a {
  color: white;
  font-size: 20px;
  padding: 7px 13px;
  border-radius: 5px;
  text-transform: capitalize;
}
a.active,
a:hover {
  background-color: rgb(108, 108, 185);
  transition: 0.5s;
}

/* CSS for other components */
.main {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card {
  width: 350px;
  height: auto;
  box-shadow: 0 0 40px 20px rgba(0, 0, 0, 0.26);
  perspective: 1000px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inner-box {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 15px 15px 15px 15px;
  background-color: rgba(54, 54, 148, 0.8);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card h2 {
  font-weight: bold;
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}
.input-box {
  width: 100%;
  height: 32px;
  border: 2px solid #fff;
  margin: 6px 0;
  border-radius: 20px;
  padding: 0 10px;
  box-sizing: border-box;
  outline: none;
  text-align: center;
}
.submit-btn {
  position: relative;
  line-height: 32px;
  font-size: 14px;
  background: rgb(187, 187, 245);
  border-radius: 10px;
  cursor: pointer;
}
.lb {
  font-size: small;
  margin-left: 115px;
}
